#intro {
    position: relative;
    margin-bottom: 400px;
}

@keyframes animStar {
    from {
        transform: translateY(0px);
    }
    to {
        transform: translateY(-2000px);
    }
}

.arrow-container {
    display: block;
    width: 100px;
    height: 100px;
    position: relative;
    left: 50%;
    transform: translate(-50%, 150%) rotateZ(0deg);
}

.arrow-container:hover {
    cursor: pointer;
}

.arrow-container:hover .arrow {
    top: 50%;
}

.arrow-container:hover .arrow:before {
    transform: translate(-50%, -50%) rotateZ(-30deg);
}

.arrow-container:hover .arrow:after {
    transform: translate(-50%, -50%) rotateZ(30deg);
}

.arrow {
    position: absolute;
    left: 50%;
    transition: all 0.4s ease;
}

.arrow:before,
.arrow:after {
    transition: all 0.4s ease;
    content: "";
    display: block;
    position: absolute;
    transform-origin: bottom right;
    background: #fff;
    width: 4px;
    height: 50px;
    border-radius: 10px;
    transform: translate(-50%, -50%) rotateZ(-45deg);
}

.arrow:after {
    transform-origin: bottom left;
    transform: translate(-50%, -50%) rotateZ(45deg);
}

.arrow:nth-child(1) {
    opacity: 0.3;
    top: 35%;
}

.arrow:nth-child(2) {
    opacity: 0.6;
    top: 55%;
}

.arrow:nth-child(3) {
    opacity: 0.9;
    top: 75%;
}

#tools-container {
    position: relative;
}

.tools-card {
    display: block;
    margin: 50px 0px;
}

.tools-card div {
    background: snow;
    display: inline-block;
    color: #0f0f0f;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 0px 15px 5px white;
    width: 28vw;
    min-height: 8vw;
    overflow-wrap: break-word;
    opacity: 0.9;
}

.tools-card div.right {
    text-align: center;
    margin-right: 15%;
}

.tools-card div.left {
    text-align: center;
    margin-left: 15%;
}

.right {
    text-align: right;
}

.left {
    text-align: left;
}

.reveal {
    position: relative;
    transform: translateY(150px);
    opacity: 0;
    transition: 2s all ease;
}

.reveal.active {
    transform: translateY(0);
    opacity: 1;
}

#navbar {
    display: flex;
    align-content: center;
    height: 100px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0px 20%;
    align-items: center;
}

#navbar div svg {
    font-size: 32px;
    margin: 0px 10px;
    cursor: pointer;
}

#navbar div {
    display: flex;
    align-items: center;
}

.button {
    background: transparent;
    border-radius: 30px;
    color: white;
    border: none;
    padding: 5px 30px;
    font-family: 'Fira Code', monospace;
    font-size: 20px;
    cursor: pointer;
    position: relative;
}

.button {
    border-color: #F5222D;
    color: white;
    box-shadow: 0 0 40px 40px #F5222D inset, 0 0 0 0 #F5222D;
    transition: all 150ms ease-in-out;
}

.button:hover {
    /* box-shadow: 0 0 10px 0 #F5222D inset, 0 0 10px 4px #F5222D; */
    transform: translate3d(0, -2px, 0);
}

#navbar div a {
    transition: all 150ms ease-in-out;
}

#navbar div a:hover {
    /* animation: shake 0.82s cubic-bezier(.36, .07, .19, .97) both; */
    transform: translate3d(0, -2px, 0);
}